.img_wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.img_wrapper > .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
}
.img_wrapper > .progress {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 5px;
    background: #fff;
    z-index: 10;
}
.img_wrapper > .logo {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-transition: all 0.5s cubic-bezier(0.17, 0.67, 0.25, 1.02);
    transition: all 0.5s cubic-bezier(0.17, 0.67, 0.25, 1.02);
    background: #1fb5ad;
    background-position: center center;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.img_wrapper > .logo > .text {
      font-family: 'Lato-Bold';
      position: absolute;
      color: #fff;
      font-size: 50px;
      letter-spacing: 5px;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%) scale(0.5);
              transform: translate(-50%, -50%) scale(0.5);
      opacity: 0;
}
.img_wrapper .img_box {
    display: block;
    float: left;
}
.img_wrapper .img_box .twoCols {
      width: 50%;
}
.img_wrapper .img_box .threeCols {
      width: 33.333333%;
}
.img_wrapper.fin > .progress {
    display: none;
}
.img_wrapper.fin > .logo {
    width: 390px;
    height: 108px;
}
.img_wrapper.fin > .logo > .text {
      -webkit-transform: translate(-50%, -50%) scale(1);
              transform: translate(-50%, -50%) scale(1);
      opacity: 1;
}

