.box {
  border: 2px solid #1fb5ad;
  width: 100%;
  height: 478px;
  overflow: hidden;
  position: relative;
}
.box > .scroll-bar {
    float: right;
    width: 25px;
    height: 100%;
    border-left: 2px solid #1fb5ad;
    position: relative;
}
.box > .scroll-bar > .scroll {
      position: absolute;
      width: 100%;
      height: 80px;
      background: #1fb5ad;
      top: 0;
      left: 0;
}
.box > .list {
    float: left;
    width: calc( 100% - 25px);
    position: absolute;
    top: 0;
    left: 0;
}
.box > .list > a {
      width: 100%;
      display: block;
      border-bottom: 2px solid #1fb5ad;
      height: 115px;
      line-height: 115px;
      padding-left: 16px;
}
